import { state } from '@angular/animations';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-form-body',
  templateUrl: './form-body.component.html',
  styleUrls: ['./form-body.component.css']
})
export class FormBodyComponent implements OnInit {

  catagory = history.state.name;
  today = new Date();
  list: any = [];
  data: any = {
    money:'' , note:'', catagory:this.catagory, date:new Date(),
  }
  constructor() { }

  ngOnInit(): void {
  }

  //assign data to array list
  addData() {
    if(this.data) {
      this.list.push(this.data);
      this.addMultipleData(this.list);
    }
  }

  //add mltiple vale to local storage
  addMultipleData(list: any){
    let dataList = [];
    if (localStorage.getItem('$expensive')) {
      dataList = JSON.parse(localStorage.getItem('$expensive')|| '{}');
      dataList = [list, ...dataList];
    } else {
      dataList = [list];
    }
    localStorage.setItem('$expensive', JSON.stringify(dataList));
    window.location.replace("/");
  }
} 
