import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-expensive-body',
  templateUrl: './expensive-body.component.html',
  styleUrls: ['./expensive-body.component.css']
})
export class ExpensiveBodyComponent implements OnInit {

  today = new Date();
  list: any = [];
  data: any = {
    money:'' , note:'', catagory:'', date:new Date(),
  }
  constructor() { }

  ngOnInit(): void { 
  }

  //assign data to array list
  addData() {
    if(this.data) {
      this.list.push(this.data);
      this.addMultipleData(this.list);
    }
  }

  //add mltiple vale to local storage
  addMultipleData(list: any){
    let dataList = [];
    if (localStorage.getItem('$expensive')) {
      dataList = JSON.parse(localStorage.getItem('$expensive')|| '{}');
      dataList = [list, ...dataList];
    } else {
      dataList = [list];
    }
    localStorage.setItem('$expensive', JSON.stringify(dataList));
    window.location.replace("/");
  }
}
