import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-income-body',
  templateUrl: './income-body.component.html',
  styleUrls: ['./income-body.component.css']
})
export class IncomeBodyComponent implements OnInit {
  
  today = new Date();
  list: any = [];

  data: any = {
    money:'' , note:'',catagory:'', date:new Date(),
  }

  constructor() { }

  ngOnInit(): void {
  }
  //assign data to array list
  addData() {
    if(this.data) {
      this.list.push(this.data);
      this.addMultipleData(this.list);
    }
  }

  //add mltiple vale to local storage
  addMultipleData(list: any){
    let dataList = [];
    if (localStorage.getItem('$income')) {
      dataList = JSON.parse(localStorage.getItem('$income')|| '{}');
      dataList = [list, ...dataList];
    } else {
      dataList = [list];
    }
    localStorage.setItem('$income', JSON.stringify(dataList));
    window.location.replace("/");
  }
}
